export default ({isAuth, values, errors}) => {
    const rules = {
        email: (value) => {
            if (!value) {
                errors.email = '–í–≤–µ–¥–∏—Ç–µ –∞–¥—Ä–µ—Å';
            } else if (
                !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
                errors.email = '–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –∞–¥—Ä–µ—Å';
            }
        },
        password: (value) => {
            if (!value) {
                errors.password = '–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å';
            } else if (!/(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}/i.test(value)) {
                errors.password = isAuth ? '–ù–µ–≤–µ—Ä–Ω—ã–π –ø–∞—Ä–æ–ª—å' : '–°–ª–∏—à–∫–æ–º –ª–µ–≥–∫–∏–π –ø–∞—Ä–æ–ª—å';
            }
        },
        /*rePassword: (value, values) => {
            if (!value) {
                errors.rePassword = '–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å';
            }
            if (values.password !== value) {
                errors.rePassword = '–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç';
            }
        }*/
    };

    Object.keys(values).forEach(key => // –¥–æ—Å—Ç–∞–µ–º –≤—Å–µ –∫–ª—é—á–∏ –∏–∑ –æ–±—ä–µ–∫—Ç–∞ –∏ –ø—Ä–º–µ–Ω—è–µ–º –¥–ª—è –∫–∞–∂–¥–æ–≥–æ —Ñ—É–Ω–∫—Ü–∏—é
        rules[key] && rules[key](values[key]) // –µ—Å–ª–∏ —Ç–∞–∫–æ–π –∫–ª—é—á –µ—Å—Ç—å, —Ç–æ –ø—Ä–∏–º–µ–Ω–∏—Ç—å —Ñ—É–Ω–∫—Ü–∏—é
    )
};